@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
a {
  text-decoration: none;
  color: #000;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper-mobile {
  padding: 0 30px;
  min-width: 260px;
}
.main-nav {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
}
.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  height: 79px;
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 29px 0 27px 76px;
  border-top: 1px solid #ececec;
}
.main-nav__link:hover .main-nav__text {
  opacity: 0.6;
}
.main-nav__link:hover .main-nav__cart,
.main-nav__link:hover .main-nav__loop {
  color: #63d1bb;
  fill: #63d1bb;
}
.main-nav__link:focus .main-nav__text,
.main-nav__link:focus .main-nav__cart,
.main-nav__link:focus .main-nav__loop {
  opacity: 0.3;
}
.main-nav__list--second-part .main-nav__item:last-child .main-nav__link {
  border-bottom: 1px solid #ececec;
}
.main-nav__cart,
.main-nav__loop {
  position: absolute;
  top: 32px;
  left: 32px;
  color: #cccccc;
  fill: #cccccc;
}
.page {
  height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #fff;
}
.page-header__top {
  position: relative;
  height: 79px;
}
.page-header__logo {
  display: block;
  padding-left: 116px;
  padding-top: 22px;
  padding-bottom: 22px;
}
.page-header__button {
  position: absolute;
  width: 21px;
  height: 20px;
  top: 29px;
  right: 30px;
  border: none;
  outline: none;
  background-image: url("../../img/page-header-menu-close-icon.svg");
  background-repeat: no-repeat;
  background-color: transparent;
}
.promo {
  height: 489px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  background-image: url("../../img/promo-background-image.jpg");
  background-repeat: no-repeat;
}
@media (min-resolution: 2ddpx) {
  .promo {
    background-image: url("../../img/promo-background-image@2x.jpg");
    background-size: 320px auto;
  }
}
.promo__title {
  margin: 0;
  padding: 0;
}
